// lost
%clearfix {
	lost-utility: clearfix; }





// 超連結
@mixin hover {
	&:hover, &:focus {
		@content; } }





// 陰影
@mixin box-shadow-z1 {
	box-shadow: $z1; }
%box-shadow-z1 {
	@include box-shadow-z1; }

@mixin box-shadow-z2 {
	box-shadow: $z2; }
%box-shadow-z2 {
	@include box-shadow-z2; }

@mixin box-shadow-z3 {
	box-shadow: $z3; }
%box-shadow-z3 {
	@include box-shadow-z3; }

@mixin box-shadow-z4 {
	box-shadow: $z4; }
%box-shadow-z4 {
	@include box-shadow-z4; }
